import $ from 'jquery';

$(() => {
  // eslint-disable-next-line func-names
  $('.tabs-list li').click(function() {
    const index = $(this).index();
    $(this)
      .addClass('active')
      .siblings()
      .removeClass('active');
    const contents = $(this)
      .parent()
      .next('.tabs-content');
    contents
      .children('.tab-pane')
      .eq(index)
      .addClass('active')
      .siblings()
      .removeClass('active');
  });
});



